
import { emitter } from "@/main";
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: {
    routeName: String,
    icon: String,
    name: String,
  },
  setup(props) {
    const hasIcon = ref(props.icon !== undefined);

    const closeNav = () => emitter.emit("close-nav");

    return {
      hasIcon,
      closeNav,
    };
  },
});
